#portfolio {
    min-height: 100vh;
    display: flex;
    padding: 2rem;
    background-image:linear-gradient(135deg,#f5f7fa 0%,#c3cfcc 100%);
    justify-content: center;
    align-items: center;
}

.timeline-container {
    min-width: 300px;
    width: 60%;
    max-height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
}

.timeline-container::after {
    content: '';
    width: 4px;
    background-color: #e17b77;
    height: 100%;
    position: absolute;
    left: calc(50% - 2px);
}

.timeline-item {
    /* background-color: #fff; */
    padding-right: 30px;
    position: relative;
    display: flex;
    margin:10px 0;
    width: 50%;
    justify-content: flex-end;
}

.timeline-item:nth-child(odd) {
    align-self: flex-end;
    justify-content: flex-start;
    padding-right: 0;
    padding-left: 30px;
}

.timeline-item-content {
    background-color: #fff;
    display: flex;
    padding: 15px;
    width: 200px;
    text-align: right;
    flex-direction: column;
    align-items: flex-end;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0,0,0,0.3);
}

.timeline-item:nth-child(odd) .timeline-item-content {
    text-align: left;
    align-items: flex-start;
}

.timeline-item-content::after {
  content: ' ';
  background-color: #fff;
  box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.2);
  position: absolute;
  right: 22.5px;
  top: calc(50% - 7.5px);
  transform: rotate(45deg);
  width: 15px;
  height: 15px;
}

.timeline-item:nth-child(odd) .timeline-item-content::after {
  right: auto;
  left: 22.5px;
  box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.2);
}

.timeline-item-content .tag {
    color:#fff;
    font-weight: bold;
    margin-top:-15px;
    margin-right: -15px;
    padding: 5px 10px;
    text-transform: uppercase;
    margin-bottom: 5px;
    font-size:12px;
}

.timeline-item:nth-child(odd) .timeline-item-content .tag {
  margin-left:-15px;
}
.timeline-item-content time {
  color: #777;
  font-size: 12px;
  font-weight: bold;
}
.timeline-item-content p {
  font-size: 16px;
  line-height: 24px;
  margin: 15px 0;
  max-width: 200px;
}
.timeline-item-content a {
  font-size: 14px;
  font-weight: bold;
}
.timeline-item-content a::after {
  content: ' â–º';
  font-size: 12px;
}
.timeline-item-content .circle {
  background-color: #fff;
  border: 3px solid #e17b77;
  border-radius: 50%;
  position: absolute;
  top: calc(50% - 10px);
  right: -10px;
  width: 20px;
  height: 20px;
  z-index: 1;
}
.timeline-item:nth-child(odd) .timeline-item-content .circle {
  right: auto;
  left: -11px;
}

.port-intro {
  background-image:linear-gradient(135deg, #f5f7fa 0%, #c3cfcc 100%);
  justify-content: center;
  width: 100%;
  display: flex;
  height: 20vh;
  align-items: center;
}

.port-intro h3 {
  margin: 0;
}